.Slider__soft-button {
  position: absolute;
  top: 0;
  z-index: 1;
  width: 10%;
  height: 100%;
  padding: 0;
  border: 0;
  background: -o-linear-gradient(
    left,
    var(--back-color) 0%,
    var(--back-color) 1%,
    rgba(0, 0, 0, 0.1) 90%,
    var(--second-color) 100%
  );
  background: -webkit-gradient(
    linear,
    left top,
    right top,
    from(var(--back-color)),
    color-stop(1%, var(--back-color)),
    color-stop(90%, rgba(0, 0, 0, 0.1)),
    to(var(--second-color))
  );
  background: linear-gradient(
    to right,
    var(--back-color) 0%,
    var(--back-color) 1%,
    rgba(0, 0, 0, 0.1) 90%,
    var(--second-color) 100%
  );
  -webkit-backdrop-filter: blur(9px) grayscale(0.9);
  backdrop-filter: blur(9px) grayscale(0.9);
  cursor: pointer;
}
